    .services__container {
  grid-template-columns: repeat(3, 300px);
  justify-content: center;
  column-gap: 1.8rem;
}

.services__content {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: var(--container-color);
  padding: 5rem 0 2rem 2.5rem;
  transition:  box-shadow 0.3s, border-color 0.3s, background-color 0.3s;
  height: 350px;
}

.services__content:hover {
  /* transform: scale(1.05); */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  border: 2px solid #516CF7;
  background-color: var(--container-color-hover); /* Define the hover background color */
  cursor: pointer;
}
.services__icon{
    display: block;
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-1);
}
.services__title{
    font-size:var(--h3-font-size);
    margin-bottom: var(--mb-1);
    font-weight: var(--font-meduim);
    white-space: pre-line;
}
.services__button{
    color: var(--title-color);
    font-size: var(--small-font-size);
    display: inline-flex ;
    align-items: center;
    column-gap: 0.25rem;
    cursor: pointer;
}
.services__button-icon{
    font-size: 1rem;
    transition: 0.3s;
}
.services__button:hover .services__button-icon{
    transform: translateX(00.25rem);
}

.services__modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color:rgba(0,0,0,0.5);
    z-index: var(--z-modal);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    opacity: 0;
    visibility: hidden;
    /* transition: .3s; */
}
.services__modal-content {
    width: 100%; /* Set width to match front content width */
    max-width: 600px; /* Add a max-width to prevent excessive growth */
    position: relative;
    background-color: var(--container-color);
    /* padding: 6rem 2.5rem 2.5rem;  */
    border-radius: 1.5rem;
}
.services__modal-close{
    position:absolute;
    top: 1.5rem;
    right: 1.5rem;
    font-size: 1.5rem;
    color: var(--title-color);
    cursor: pointer;
    
}
.services__modal-title,
.services__modal-description{
    text-align: center;
    margin-top: 0 !important;
    padding: 0;
}
.services__modal-title{
    display: flex;
    justify-content: center;
    align-items: baseline;
    font-size: var(--h3-font-size);
    font-weight: var(--font-meduim);
    margin-bottom: var(--mb-1);
    margin-top: 0 !important;
}
.services__modal-title>.services__icon{
   font-size: 1.6rem;
   margin-right: 4px;
}

.services__modal-description{
    font-size: var(--small-font-size);
    /* padding: 0 3.5rem; */
    margin-bottom: var(--mb-2);
}
.services__modal-services{
    row-gap: 0.75rem;
    margin-left: 10px;
    /* gap: 0px;; */
}
.services__modal-service{
    display: flex;
    align-items: center;
    /* justify-content: center; */
    column-gap: 3px;
    margin-left: 16px;


}
.service__modal-icon{
    color: var(--title-color);
    font-size: 1.1rem;
}
.services__modal-info{
    font-size: var(--small-font-size);
}

.active-modal {
    opacity: 1;
    visibility: visible;
    display: flex;
    /* justify-content: center; */
    /* align-items: center; */
    position: relative;
    border: 2px solid rgba(0, 0, 0, 0.1);
    background-color: var(--container-color);
    /* padding: 6rem 0 2rem 2.5rem; Padding matches front content */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    border: 2px solid #516CF7;
    cursor: pointer;
    z-index: var(--z-modal);
    height: 350px; /* Set a fixed height that matches the front content's height */
    overflow: hidden; /* Hide any overflow content if necessary */
}

/* ...remaining styles... */


/* ...remaining styles... */


/* dark mode */
#dark .services__content{
    background-color: #BCC5CE;
}
#dark .services__title{
    color: #242132;
}
#dark .services__button{
    color: #242132ba;
}
/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
.services__container{
    grid-template-columns: repeat(3,218px);
}
}

/* For medium devices */
@media screen and (max-width: 768px) {
.services__container{
        grid-template-columns: repeat(2,1fr);
}
.services__content{
    padding: 3.5rem 0.5rem 1.25rem 1.5rem;
}
.services__icon{
    font-size: 1.5rem;
}
}

@media screen and (max-width: 576px) {
    .services__modal-content{
        padding:4.5rem 1.5rem 2.5rem ;
    }
    .services__modal-description{
        padding: 0;
    }
}

/* For small devices */
@media screen and (max-width: 350px) {
    .services__container{
        grid-template-columns: max-content;
}
.services__content{
    padding-right: 6rem;
}
}


