.home__container {
    row-gap: 7rem;
    min-height: 100vh;

}


.cover {
    background-image: url('https://github.com/ToniRajamaki/folio2/blob/minimal/public/assets/toni/dot-grid.png?raw=true');
    background-position: center; /* Centers the background image */
    height: 100vh; /* Covers the full viewport height */
    width: 100vw; /* Covers the full viewport width */
    position: fixed; /* Stays in place relative to the viewport */
    background-repeat: repeat;
    opacity: 0.4; /* 80% opacity */
    z-index: -1; /* Keeps it behind other content */
    transform: rotate(180deg); /* Rotates the background */
    top: 0; /* Aligns the top edge with the viewport */
    left: 0; /* Aligns the left edge with the viewport */
  }
  

.home{
    padding-bottom: 0 !important;
    
}

.home__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    flex-direction: row;
    gap: 1.5rem;
    padding-top: 5.5rem;
    align-items: center;
}p

/* #light  .home__content{
        color: #000000;
    } */
.home__social {
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}

.home__social-icon {
    font-size: 1.25rem;
    color: white;
    padding-left: 20px;
}

.home__social-icon:hover {
    color: var(--title-color-dark);
}

.home__title {
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
}

.home__hand {
    width: 38px;
    height: 38px;
    margin-left: 0.4rem;
}

.home_subtitle {
    position: relative;
    font-size: var(--h3-font-size);
    padding-left: 2.8rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-1);
    letter-spacing: 0.4rem;
}

.home_subtitle::before {
    content: '';
    position: absolute;
    width: 40px;
    height: 1px;
    background-color: var(--text-color);
    left: 0;
    top: 1rem;
}


.home__description {
    margin-bottom: 2em;
    max-width: 35em;
}

.home__img {
    /* mix-blend-mode: multiply; */
    background-position: center;
    background-size: cover;
    order: 1;
    justify-self: center;
    width: 450px;
    height: 450px;
    transition: transform 0.4s ease-in-out; /* Adjust the transition duration */
  }
  
  .home__img:hover {
    transform: scale(1.05); /* Slightly increase the size on hover */
  }
  


@keyframes profile__animate {
    0% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }

    50% {
        border-radius: 30% 60% 70% 40%/50% 60% 30% 40%;
    }

    100% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}

.home__scroll {
    margin-left: 9.25rem;

}

.wheel {
    animation: scroll 1s ease infinite;
}

@keyframes scroll {
    0% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(3.75rem);
    }
}

.home__scroll-name {
    color: var(--title-color);
    font-weight: var(--font-meduim);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
}

.home__scroll-arrow {
    font-size: 1.25rem;
    color: var(--text-color);
}


/* -----dark mode home ---- */

#dark .home__title {
    color: #ae8cfa;
}

#dark .home__content {
    color: #fefcff;
}

#dark .home__social-icon {
    font-size: 1.25rem;
    color: #ae8cfa;

}

#dark .home__social-icon:hover {
    font-size: 1.25rem;
    color: #746cfe;
}

#dark .home__hand {
    color: #ae8cfa;
}

#dark .home_subtitle {
    color: #BCC5CE;
}

#dark .button {
    color: #fefcff;
    background: rgb(174, 140, 250);
    background: radial-gradient(circle, rgba(174, 140, 250, 1) 0%, rgba(116, 108, 254, 1) 97%, rgba(174, 140, 250, 1) 100%);
}


#dark .home__scroll {
    margin-left: 9.25rem;
    color: #746cfe;

}

#dark .wheel {
    animation: scroll 1s ease infinite;

}


#dark .home__scroll-name {
    color: #746cfe;
}

#dark .home__scroll-arrow {
    color: #746cfe;

}
#dark .home__description{
    color: rgba(220, 220, 220, 0.817);
}




/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .home__content {
        grid-template-columns: 100px repeat(2, 1fr);
        column-gap: 1.25rem;
    }

    .home__hand {
        width: 26px;
        height: 26px;
    }

    .home_subtitle {
        padding-left: 3.75rem;
        margin-bottom: var(--mb-1);
    }

    .home_subtitle::before {
        width: 42px;
        top: 0.8rem;
    }

    .home__description {
        min-width: initial;
        margin-bottom: var(--mb-2-5);
    }

   

    .home__scroll {
        margin-left: 7.5rem;
    }
}


 @media (max-width: 1200px){
    .home__img{
        width: 200px;
        height: 200px;
    }
 }

/* For medium devices */
@media screen and (max-width: 768px) {
    .home__content {
        grid-template-columns: 0.5fr 3fr;
        padding-top: 3.5rem;
    }
    .home__title {
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
    padding-left: 20px;
}
   .home__description {
        min-width: initial;
        margin-bottom: var(--mb-2-5);
        padding-left: 20px;
                padding-right: 20px;

    }
    .typed-text{
        padding-left: 20px;
    }
  
 .button .button--flex {
    padding-left: 50px;
 }
    .home__data {
    }

    .home__img {
        width: 200px;
        height: 200px;
        box-shadow: inset 0 0 0 1px rgb(255 255 255 / 30%);
    }

    .home__scroll {
        display: none;
    }
 
    .SeeMyProjectsButton{
        align-items: left;
        justify-self: left;
    }

}

