/* For small devices */
@media screen and (max-width: 350px) {
    .home__img {
        width: 180px;
        height: 180px;
    }

    .home__hand {
        width: 22px;
        height: 22px;
    }
    
    .waves{
        display: none;
    }
}
.waves{
    position: absolute;
    display: block;
    transform: rotate(180deg);
    width: 100em !important;
    top: 4em;
    width: 500em !important;
}

.wave1 {
    border-bottom: -5em;
    position: relative;
    bottom: 1em;
    left: 0;
    width: 100%;
    z-index: 1;
}








  